<template>
  <div class="detail">
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      fixed
      @click-left="back"
    />

    <div class="detail-box">
      <div class="detail-img">
        <img class="auto-img" :src="product.large_img" alt="" />
      </div>

      <div class="detail-info">
        <div class="d-pro-name">
          <div>
            <div class="d-zhname">{{ product.name }}</div>
            <div class="d-enname">{{ product.enname }}</div>
          </div>
          <div class="d-price">&yen;{{ product.price }}</div>
        </div>

        <div class="d-rules">
          <div class="d-rule" v-for="(v1, i1) in product.rules" :key="i1">
            <div class="d-rule-title">{{ v1.title }}</div>
            <div class="d-rule-items">
              <div
                class="d-rule-item"
                :class="{ active: v1.activeIndex === i2 }"
                v-for="(v2, i2) in v1.subRule"
                :key="i2"
                @click="selectRule(v1, i2)"
              >
                {{ v2.title }}
              </div>
            </div>
          </div>
        </div>

        <div class="select-count">
          <div class="d-count-title">选择数量</div>
          <div class="d-count">
            <van-stepper
              v-model="count"
              theme="round"
              button-size="22"
              disable-input
            />
          </div>
        </div>

        <div class="d-desc">
          <div class="d-desc-title">商品描述</div>
          <div class="d-desc-info">
            <div
              class="d-desc-content"
              v-for="(item, index) in product.desc"
              :key="index"
            >
              {{ index + 1 }}、{{ item }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-goods-action>
      <!-- icon="bag"  设置图标 -->
      <van-goods-action-icon
        ref="shopbag"
        icon="bag"
        :badge="$root.publicShopbagCount"
        color="#646566"
        @click="goPage('Shopbag')"
      />
      <van-goods-action-icon
        icon="like"
        :text="isLike ? '已收藏' : '未收藏'"
        :color="isLike ? '#eb2403' : '#646566'"
        @click="likeAndNotlike"
      />
      <van-goods-action-button
        color="#ccc"
        text="加入购物袋"
        @click="addShopbag"
      />
      <van-goods-action-button
        color="#eb2403"
        text="立即购买"
        @click="toConfirm(pid)"
      />
      <!-- @click="toConfirm(pid)" -->
    </van-goods-action>
  </div>
</template>

<script>
export default {
  name: "Detail",
  data() {
    return {
      count: 1,

      pid: "",

      product: {},

      isLike: false,

      //购物袋数量
      shopbagCount: 0,

      //保存徽章节点
      badgeNode: null,
    };
  },

  created() {
    //截取商品pid
    this.pid = this.$route.params.pid;

    this.getProductByPid(this.pid);

    this.findUserLike();
    // 计算数量
    if (this.$root.publicShopbagCount === "") {
      this.getShopbagCount();
    }

    //获取dom节点
    this.$nextTick(() => {
      //
      // 获取指定的元素
      this.badgeNode = this.$refs.shopbag.$el.querySelector(".van-info");
    });
  },

  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    toConfirm(p1) {
      console.log(p1);
      //   // 获取提交的商品
      // let pone = [];
      // pone.push(p1);
      // this.shopbagData.forEach(v =>{
      //   if(v.checked){

      //   }
      // })
      // if(sids.length ==0){
      //    this.$toast("请选择商品");
      //    return;
      // }
      // this.$router.push({name: 'ConfirmOrder', query: {sids: sids.join("-")}});
      this.$router.push({ name: "ConfirmOrder", query: { pone: p1 } });
    },
    // 跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //根据商品pid获取商品数据
    getProductByPid(pid) {
      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          pid,
        },
      })
        .then((res) => {
          /**
           * [
              {
                title: '温度',
                activeIndex: 0,
                subRule: [
                  {title: '默认奶油'},
                  {title: '无奶油'},
                ]
              }
           */
          //
          if (res.data.code === 600) {
            //处理商品描述
            let data = res.data.result[0];
            //
            data.desc = data.desc.split(/\n/); //换行进行切割成数组
            //

            //处理规格
            let rules = ["cream", "milk", "sugar", "tem"]; //sugar: "全糖/半糖"
            let rulesData = [];
            rules.forEach((v) => {
              //排除空规格
              if (!data[v]) {
                return;
              }

              let rule = {
                title: data[`${v}_desc`],
                //当前规格激活的下标
                activeIndex: 0,
                //子规格
                subRule: [],
              };

              //获取子规格数据

              let subRuleData = data[v].split(/\//);
              subRuleData.forEach((item) => {
                rule.subRule.push({ title: item });
              });

              rulesData.push(rule);
            });

            data.rules = rulesData;

            this.product = data;
          }
        })
        .catch((err) => {});
    },

    //如果用户已经登录，则根据商品pid查询当前商品是否被收藏
    findUserLike() {
      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          pid: this.pid,
          tokenString,
        },
      })
        .then((res) => {
          if (res.data.code === 1000 && res.data.result.length === 1) {
            //当前商品已经被用户收藏
            this.isLike = true;
          }
        })
        .catch((err) => {});
    },

    //收藏或者取消收藏商品
    likeAndNotlike() {
      //根据isLike判断商品是否被收藏
      let url = "";
      if (this.isLike) {
        //如果商品已经被收藏, 则执行取消收藏
        url = "/notlike";
      } else {
        url = "/like";
      }

      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "POST",
        url,
        //如果是post请求, 参数格式应为 key1=value1&key2=value2...
        data: {
          pid: this.pid,
          tokenString,
        },
      })
        .then((res) => {
          this.$toast(res.data.msg);

          if (res.data.code == 700) {
            //用户未登录, 拦截操作, 通知用户去登录
            this.$toast("请先登录");
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 2000);

            return;
          }

          if (res.data.code === 800) {
            //收藏商品
            this.isLike = true;
          } else if (res.data.code === 900 && res.data.result === 1) {
            //取消收藏商品
            this.isLike = false;
          }
        })
        .catch((err) => {});
    },

    //选择商品规格
    selectRule(item, index) {
      if (item.activeIndex === index) {
        return;
      }

      item.activeIndex = index;
    },

    //如果用户已经登录，则查询购物袋的商品总数量
    getShopbagCount() {
      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "GET",
        url: "/shopcartCount",
        params: {
          tokenString,
        },
      })
        .then((res) => {
          if (res.data.code === 4000) {
            this.$root.publicShopbagCount = res.data.result;
          }
        })
        .catch((err) => {});
    },
    //加入购物袋
    addShopbag() {
      //获取商品规格
      let rules = this.product.rules;

      let selectRule = [];

      rules.forEach((v) => {
        selectRule.push(v.subRule[v.activeIndex].title);
      });

      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          pid: this.pid,
          count: this.count,
          rule: selectRule.join("/"),
          tokenString,
        },
      })
        .then((res) => {
          this.$toast(res.data.msg);

          if (res.data.code === 3000) {
            //修改购物袋显示的数量
            this.$root.publicShopbagCount += this.count;

            //抖动购物袋的徽章
            this.badgeNode.className = "van-info shake";

            setTimeout(() => {
              this.badgeNode.className = "van-info";
            }, 500);
          }
        })
        .catch((err) => {});
    },
  },
};
</script>

<style lang="less" scoped>
.detail {
  padding-top: 46px;
  padding-bottom: 50px;

  /deep/ .van-info.shake {
    animation: shake 0.4s linear;
  }

  //购物袋徽章抖动
  @keyframes shake {
    0% {
      top: 0;
    }

    25% {
      top: -8px;
    }

    50% {
      top: 8px;
    }

    100% {
      top: 0;
    }
  }

  .d-desc {
    margin-top: 20px;
  }

  .d-desc-title {
    font-size: 14px;
    margin-bottom: 10px;
  }

  .d-desc-content {
    margin-bottom: 5px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .d-desc-info {
    color: #666;
  }

  .select-count {
    margin-top: 30px;
    display: flex;
    border-top: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    padding: 15px 0;
  }

  .d-count {
    margin-left: auto;
  }

  .d-count-title {
    font-size: 14px;
  }

  /deep/ .van-nav-bar .van-icon {
    color: #eb2403;
  }

  /deep/ .van-nav-bar__text {
    color: #eb2403;
    // color:#646566;
  }

  /deep/ .van-stepper--round .van-stepper__plus {
    background-color: #eb2403;
  }

  /deep/ .van-stepper--round .van-stepper__minus {
    color: #eb2403;
    border-color: #eb2403;
  }

  .detail-info {
    position: relative;
    top: -20px;
    margin: 0 10px;
    padding: 10px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .d-pro-name {
    display: flex;
    align-items: center;
  }

  .d-price {
    margin-left: auto;
    font-size: 14px;
    color: #e4393c;
    font-weight: bold;
  }

  .d-zhname {
    font-size: 14px;
    font-weight: bold;
  }

  .d-enname {
    color: #666;
  }

  .d-rules {
    margin-top: 30px;
  }

  .d-rule {
    display: flex;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .d-rule-title {
    width: 50px;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
  }

  .d-rule-items {
    display: flex;
  }

  .d-rule-item {
    width: 60px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    color: #666;
    background-color: #e8e8e8;
    margin-right: 10px;
    border-radius: 13px;
    &:last-child {
      margin-right: 0;
    }
    &.active {
      background-color: #eb2403;
      color: #fff;
    }
  }
}
</style>